import wx from 'weixin-js-sdk'

export function wxInit (sd) {
  let links = `${window.location.href}`
  let title = sd.title
  let desc = sd.summary
  let imgUrl = sd.thumb
  wx.ready(function () {
    wx.onMenuShareTimeline({
      title: title, // 
      desc: desc, // 
      link: links, // 
      imgUrl: imgUrl, // 
      success: function () {
      },
      cancel: function () {
      }
    })
    // 
    wx.onMenuShareAppMessage({
      title: title, // 
      desc: desc, // 
      link: links, // 
      imgUrl: imgUrl, // 
      success: function () {
      },
      cancel: function () {
      }
    })
    wx.onMenuShareQQ({
      title: title, // 
      desc: desc, // 
      link: links, // 
      imgUrl: imgUrl, // 
      success: function () {
      },
      cancel: function () {
      }
    })
    wx.onMenuShareWeibo({
      title: title, // 
      desc: desc, // 
      link: links, // 
      imgUrl: imgUrl, // 
      success: function () {
      },
      cancel: function () {
      }
    })
    wx.onMenuShareQZone({
      title: title, // 
      desc: desc, // 
      link: links, // 
      imgUrl: imgUrl, // 
      success: function () {
      },
      cancel: function () {
      }
    })
  })
  wx.error(function (res) {
    // configerrorconfigdebugresSPA
  })
}



